@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family:'poppins', sans-serif;
}
/*Menu de navegaci√≥n*/
.nav{
  display: flex;
  position: fixed;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 12vh;
  background-color: black;
}
.nav__logo{
  color: white;
  font-size: 28px;
  font-weight: bold;
  padding-left: 15px;
}
.menu{
  display: flex;
}
.menu li{
  color: white;
  list-style: none;
  margin: 0 15px 0 15px;
}
/*404 NOT FOUND STYLES*/
.not_found{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: black;
}
.minion__found{
  width: 350px;
  height: 300px;
}
.found__center{
  text-align: center;
}
.title__found{
  color: white;
  font-size: 60px;
}
.subtitle__found{
  font-size: 45px;
  color: white;
}
/*Menu del login*/
.login{
  display: flex;
  height: 100vh;
  background-color: black;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.title{
  color: white
}
.form{
  display: flex;
  width: 350px;
  padding: 10px;
  height: 300px;
  justify-content: center;
  flex-direction: column;
  background: gray;
}
.input__group{
  position: relative;
}
.input__group i{
  position: absolute;
  top:8px;
  left: 23px;
}
.input{
  width: 300px;
  height: 45px;
  margin-bottom : 5px;
  padding : 5px;
  border-radius: 10px;
  border: none;
  outline: none;
  padding:  0 0 0 30px;
}
.submits{
  display: flex;
  align-items: center;
  flex-direction: column;
}
.submits button{
  width: 300px;
  transition: .3s ease-in-out;
}
.submits button:hover{
  color: white;
  background-color: black;
  width: 300px;
}
